<template>
    <div id="view-project">
        <head-top></head-top>
        <top-nav></top-nav>
        <div class="main-container">
            <project-list ref="projectlist" @editSuccess="editSuccess"></project-list>
            <div class="nav-right view row-container">
                <div class="page-title" v-show="id">
                    查看"{{project.name}}"
                    <el-tooltip class="item" effect="dark" content="编辑" placement="top-start">
                        <el-button :disabled="userInfo.username.toLowerCase()!=='admin'" type="text" icon="el-icon-edit" class="margin-left" @click="editClick"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
                        <el-button :disabled="userInfo.username.toLowerCase()!=='admin'" type="text" icon="el-icon-delete" class="margin-left"
                                   @click="deleteClick"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="删除供暖季" placement="top-start">
                        <el-button :disabled="userInfo.username.toLowerCase()!=='admin'" type="text" icon="el-icon-delete" class="margin-left hot-delete"
                                   @click="seasonDeleteClick(true)"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="删除供冷季" placement="top-start">
                        <el-button :disabled="userInfo.username.toLowerCase()!=='admin'" type="text" icon="el-icon-delete" class="margin-left cold-delete"
                                   @click="seasonDeleteClick(false)"></el-button>
                    </el-tooltip>
                </div>
                <div class="margin-top" v-show="id">
                    <!--<div class="margin-bottom"><span class="ieds-dot"></span>基本信息</div>-->
                    <el-row>
                        <!--<el-col :span="2">项目名称:</el-col>-->
                        <!--<el-col :span="6">{{project.name ? project.name : '&nbsp'}}</el-col>-->
                        <el-col :span="2">项目地点:</el-col>
                        <el-col :span="5">{{project.location ? project.location : '&nbsp'}}</el-col>
                        <el-col :span="2">建筑面积:</el-col>
                        <el-col :span="3">
                            {{project.area ? project.area : '&nbsp'}}
                            <span v-if="project.area">m<sup>2</sup></span>
                        </el-col>
                        <el-col :span="2">建筑类型:</el-col>
                        <el-col :span="3">{{project.type ? project.type : '&nbsp'}}</el-col>
                        <el-col :span="2">投用时间:</el-col>
                        <el-col :span="4">{{project.beginTime | dateFormatterFilter}}</el-col>
                    </el-row>
                    <!--<el-row>
                        <el-col :span="2">建筑面积:</el-col>
                        <el-col :span="6"> {{project.area ? project.area : '&nbsp'}}<span
                                v-if="project.area">m<sup>2</sup></span></el-col>
                        <el-col :span="2">建筑类型:</el-col>
                        <el-col :span="6">{{project.type}}</el-col>
                    </el-row>-->

                    <div class="margin-bottom margin-top-big"><span class="ieds-dot"></span>参数表</div>

                    <el-table
                            :data="tableData"
                            stripe>
                        <el-table-column
                                prop="hot"
                                label="冷暖季节"
                                min-width="98px"
                                fixed
                                :filters="[{ text: '供暖季', value: true }, { text: '供冷季', value: false }]"
                                :filter-method="filterTag"
                                filter-placement="bottom-end">
                            <template slot-scope="scope">
                                <span>{{scope.row.time}}</span>
                                <el-tag style="padding: 0 4px" :type="scope.row.hot? 'primary' : 'success'"
                                        close-transition>{{scope.row.hot ? '供暖季' : '供冷季'}}
                                </el-tag>
                            </template>
                        </el-table-column>

                        <el-table-column
                                prop="tui"
                                :render-header="renderTuiHeader"
                                min-width="120px">
                        </el-table-column>

                        <el-table-column
                                prop="tuo"
                                min-width="125px"
                                :render-header="renderTuoHeader"
                                label="用户侧回水温度tuo">
                        </el-table-column>

                        <el-table-column
                                prop="tgi"
                                min-width="132px"
                                :render-header="renderTgiHeader"
                                label="地源侧供水温差tgi">
                        </el-table-column>

                        <el-table-column
                                prop="tgo"
                                min-width="135px"
                                :render-header="renderTgoHeader"
                                label="地源侧回水温度tgo">
                        </el-table-column>

                        <el-table-column
                                prop="gu"
                                min-width="89px"
                                :render-header="renderGuHeader"
                                label="用户侧流量Gu">
                        </el-table-column>

                        <el-table-column
                                prop="gg"
                                min-width="90px"
                                :render-header="renderGgHeader"
                                label="地源侧流量Gg">
                        </el-table-column>
                        <el-table-column
                                prop="pl"
                                min-width="80px"
                                :render-header="renderP1Header"
                                label="机组总耗电P1">
                        </el-table-column>
                        <el-table-column
                                prop="pu"
                                min-width="116px"
                                :render-header="renderPuHeader"
                                label="用户侧水泵总耗电Pu">
                        </el-table-column>
                        <el-table-column
                                prop="pg"
                                min-width="116px"
                                :render-header="renderPgHeader"
                                label="地源侧水泵总耗电Pg">
                        </el-table-column>
                        <el-table-column
                                prop="p"
                                min-width="80px"
                                label="系统总耗电P/kW">
                        </el-table-column>
                        <!--<el-table-column-->
                        <!--label="冷暖季节"-->
                        <!--:filters="[{ text: '供暖季', value: true }, { text: '供冷季', value: false }]"-->
                        <!--:filter-method="filterTag"-->
                        <!--filter-placement="bottom-end">-->
                        <!--<template slot-scope="scope">{{ scope.row.hot ? '供暖季' : '供冷季' }}</template>-->
                        <!--</el-table-column>-->
                        <el-table-column
                                prop="p2"
                                min-width="80px"
                                :render-header="renderP2Header">
                        </el-table-column>
                        <!--<el-table-column
                                prop="time"
                                label="时间">
                        </el-table-column>-->
                    </el-table>

                    <!-- <div class="margin-bottom"><span class="ieds-dot"></span>特征值</div>

                     <el-table
                             :data="tableData"
                             stripe
                             style="width: 100%">
                         <el-table-column
                                 prop="time"
                                 label="时间"
                                 width="180">
                         </el-table-column>
                         <el-table-column
                                 prop="tgo"
                                 :render-header="renderHeader1"
                                 width="180">
                         </el-table-column>
                         <el-table-column
                                 prop="tg"
                                 :render-header="renderHeader2">
                         </el-table-column>
                         <el-table-column
                                 prop="a"
                                 label="α">
                         </el-table-column>
                         <el-table-column
                                 prop="n"
                                 label="η">
                         </el-table-column>

                         <el-table-column
                                 prop="cop"
                                 label="COP">
                         </el-table-column>
                         <el-table-column
                                 prop="eer"
                                 label="EER">
                         </el-table-column>
                     </el-table>-->

                    <el-pagination v-if="totalCnt!=0"
                                   @size-change="handleSizeChange"
                                   @current-change="handleCurrentChange"
                                   :current-page="currentPage"
                                   :page-sizes="[2, 5,10, 50]"
                                   :page-size="pageSize"
                                   layout="total, sizes, prev, pager, next, jumper"
                                   :total="totalCnt">
                    </el-pagination>

                </div>


            </div>
        </div>
    </div>
</template>
<script>
    import headTop from '../../Header.vue'
    import footerBottom from '../../Footer.vue'
    import topNav from '../../TopNav.vue'
    import projectList from './ProjectList.vue'

    export default {
        data() {
            return {
                userInfo: this.$store.state.user,

                id: '',
                project: {},
                tableData: [],
                allEigenvalues: [],
                currentPage: 1,
                pageSize: 10,//每页多少行
                totalCnt: 0
            }
        },
        components: {
            headTop,
            footerBottom,
            topNav,
            projectList
        },
        watch: {
            '$route'(to, from){
                this.id = this.$route.query.id;
                if (this.id) {
                    this.getItem();
                } else {
                    this.project = {};
                }
            }
        },

        created() {
            this.id = this.$route.query.id;
            if (this.id) {
                this.getItem();
            } else {
                this.$router.push({path: '/projects'});
            }
        },
        methods: {
            editSuccess(name){
                this.getItem();
            },
            getItem(url){
                var self = this;
                var url = GSHP_SERVER_ENDPOINT + '/project/projectAllParams';
                var model = {id: this.$route.query.id};

                self.$http.get(url, {params: model})
                    .then(function (response) {
                        if (response.status === 200) {
                            self.successCallback(response.data);
                        }
                    })
                    .catch(function (error) {

                    });
            },

            successCallback(data){
                var data = data;

                var obj = {
                    id: data.id,
                    name: data.project_name,
                    location: data.position,
                    area: data.area,
                    type: data.type,
                    beginTime: data.begin_time,
                    paramvalues: data.table
                };

//                this.hotArr=data.table.filter((item) => {
//                    return item.hot;
//                });
//                this.coldArr=data.table.filter((item) => {
//                    return !item.hot;
//                });
                this.project = obj;
                this.totalCnt = data.table.length;
                this.handleCurrentChange(1);
            },

            /*renderTuiHeader(createElement, {column, $index}) {
             return createElement(
             'span',
             {
             'class': ''
             },
             ['用户侧供水温度', createElement('span', {
             'class': 'block'
             }, ['(从机组到用户)t', createElement('sub', {
             attrs: {type: 'text'},

             }, ['u,i']
             ), '/℃']
             )
             ]
             );
             },*/
            renderTuiHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['用户侧供水温度(从机组到用户)t',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['u,i']
                        ), '/℃'
                    ]
                );
            },

            renderTuoHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['用户侧回水温度(从用户到机组)t',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['u,o']
                        ), '/℃'
                    ]
                );
            },

            renderTgiHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['地源侧供水温差(从机组到地埋管)t',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['g,i']
                        ), '/℃'
                    ]
                );
            },

            renderTgoHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['地源侧回水温度(从地埋管到机组)t',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['g,o']
                        ), '/℃'
                    ]
                );
            },

            renderGuHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    [
                        '用户侧流量',
                        createElement('span', {
                                'class': 'block'
                            }, ['G',
                                createElement('sub', {
                                        attrs: {type: 'text'},

                                    }, ['u']
                                ),
                                '/(m',
                                createElement('sup', {
                                    attrs: {type: 'text'},

                                }, ['3']), '*', 'h', createElement('sup', {
                                    attrs: {type: 'text'},

                                }, ['-1']), ')'
                            ]
                        )
                    ]
                );
            },

            renderGgHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    [
                        '地源侧流量',
                        createElement('span', {
                                'class': 'block'
                            }, ['G',
                                createElement('sub', {
                                        attrs: {type: 'text'},

                                    }, ['g']
                                ),
                                '/(m',
                                createElement('sup', {
                                    attrs: {type: 'text'},

                                }, ['3']), '*', 'h', createElement('sup', {
                                    attrs: {type: 'text'},

                                }, ['-1']), ')'
                            ]
                        )
                    ]
                );

            },

            renderP1Header(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['机组总耗电P',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['1']
                        ), '/kW'
                    ]
                );
            },

            renderPuHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['用户侧水泵总耗电P',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['u']
                        ), '/kW'
                    ]
                );
            },

            renderPgHeader(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['地源侧水泵总耗电P',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['g']
                        ), '/kW'
                    ]
                );
            },

            renderP2Header(createElement, {column, $index}) {
                return createElement(
                    'span',
                    {
                        'class': ''
                    },
                    ['水泵总耗电P',
                        createElement('sub', {
                                attrs: {type: 'text'},

                            }, ['2']
                        ), '/kW'
                    ]
                );
            },


//            renderHeader2(createElement, {column, $index}) {
//                return createElement(
//                    'span',
//                    {
//                        'class': ''
//                    },
//                    ['∆t',
//                        createElement('sub', {
//                                attrs: {type: 'text'},
//
//                            }, ['g']
//                        )
//                    ]
//                );
//            },

            handleSizeChange(val) {
                this.pageSize = val;
                this.handleCurrentChange(1);
            },

            handleCurrentChange(val) {
                this.currentPage = val;
                this.tableData = this.project.paramvalues.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
            },

            editClick(){
                this.$refs.projectlist.projectAdd(true);
//                this.$router.push({path: '/projects/edit', query: {id: this.$route.query.id}});
            },

            deleteClick() {
                var self = this;
                this.$confirm('此操作将永久删除该项目以及该项目的所有信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    var url = GSHP_SERVER_ENDPOINT + '/projects?id=' + self.$route.query.id;
                    self.$http.delete(url)
                        .then(function (response) {
                            console.log(response);
                            if (response.status === 200) {
                                self.$message({
                                    type: 'success',
                                    message: '删除项目"' + self.project.name + '"成功!'
                                });
                                self.$router.push({path: '/projects'});
                            }
                        })
                        .catch(function (error) {
                            self.$message({
                                type: 'error',
                                message: '删除失败!'
                            });
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            seasonDeleteClick(isHot) {
                var self = this;
                this.$confirm('此操作将永久删除该项目的' + (isHot ? '供暖季' : '供冷季') + '数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    var url = GSHP_SERVER_ENDPOINT + '/projects/deleteSeasonData?id=' + self.$route.query.id + '&isHot=' + isHot;
                    self.$http.delete(url)
                        .then(function (response) {
                            console.log(response);
                            if (response.status === 200) {
                                self.$message({
                                    type: 'success',
                                    message: '删除"' + self.project.name + '"的' + (isHot ? '供暖季' : '供冷季') + '数据成功!'
                                });
                                self.getItem();
                                //self.$router.push({path: '/projects/view',query:{id:self.$route.query.id}});
                            }
                        })
                        .catch(function (error) {
                            self.$message({
                                type: 'error',
                                message: '删除失败!'
                            });
                        });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            filterTag(value, row) {
                return row.hot === value;
            },
        }
    }
</script>
<style>
    .ieds-dot {
        display: inline-block;
        height: 8px;
        width: 8px;
        border-radius: 4px;
        background-color: #409EFF;
        margin-right: 15px;
    }

    #view-project .el-row {
        /*margin-left: 28px;*/
    }

    #view-project .edit-btn {
        margin-left: 20px;
    }

    .block {
        display: block;
    }

    /*.page-bottom-btn{
        float: right;
        margin-top: 20px;
    }*/
</style>


